import React from "react";
import "./App.css";
import axios from "axios";
import Cocktail from "./Cocktail";
import Ingredients from "./Ingredients";

class App extends React.Component {
  state = {
    isLoading: true,
    drinks: [],
  };
  getCocktail = async () => {
    const {
      data: { drinks },
    } = await axios.get(
      "https://www.thecocktaildb.com/api/json/v1/1/search.php?s=gin"
    );
    const removeLastItem = drinks.pop();
    console.log(drinks);
    this.setState({ drinks, isLoading: false });
  };

  componentDidMount() {
    this.getCocktail();
  }
  render() {
    const { isLoading, drinks } = this.state;
    return (
      <div>
        {isLoading ? (
          <div className="loading">
            <span className="loading_text">Loading...</span>
          </div>
        ) : (
          <div>
            {drinks.map((drink) => {
              let recipe = {
                ingredient: [],
                measurement: [],
              };
              let ingredientIndex = 1;
              do {
                if (!drink[`strIngredient${ingredientIndex}`]) break;
                recipe.ingredient.push(
                  drink[`strIngredient${ingredientIndex}`]
                );
                recipe.measurement.push(drink[`strMeasure${ingredientIndex}`]);
                ingredientIndex += 1;
              } while (!!drink[`strIngredient${ingredientIndex}`]);
              console.log(recipe);
              // console.log(recipe.ingredient);
              return (
                <Cocktail
                  key={drink.idDrink}
                  name={drink.strDrink}
                  image={drink.strDrinkThumb}
                  ingredients={recipe.ingredient}
                  // ingredients={recipe.ingredient}
                  measurements={recipe.measurement}
                  instruction={drink.strInstructions}
                  glass={drink.strGlass}
                />
              );
            })}
          </div>
        )}
      </div>
    );
  }
}

export default App;
